.main{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    gap: 30px;
}
.stud h2{
    text-align: center;
    border: 1px solid black;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 70px;
    background: linear-gradient(90deg, rgba(174,238,174,1) 20%, rgba(206,131,210,1) 80%);

}

.stud{
    display: flex;
    flex-direction: column;
}

.form{
    display: flex;
    flex-direction: column;
    gap:20px;
    margin-top: 50px;
    align-items: center;
}

.form input{
    height: 25px;
    width: 180px;
    
}

.form button{
    background-color:yellowgreen;
    padding: 10px;
    border: 3px solid  rgb(176, 93, 255);
    font-weight: bold;
    transition: opacity linear 0.3s, transform linear 0.3s;

}



.form button:hover {
    opacity: 0.8;
    transform: translateY(5px);
    background: black;
    color: white;
  }