.div_student {
  background: linear-gradient(
    90deg,
    rgba(174, 238, 174, 0.9332983193277311) 0%,
    rgba(206, 131, 210, 0.8828781512605042) 100%
  );
  border: 3px solid black;
  border-radius: 15px;
  width: 150px;
  height: 150px;
  text-align: center;
  padding: 5px;
  margin-top: 30px;
  font-style: italic;
  font-weight: bold;
}

.div_student:hover {
  opacity: 0.8;
  background: linear-gradient(0deg, rgba(37,27,47,1) 13%, rgba(143,104,176,1) 68%);background: rgba(169, 163, 175, 1);
  transition: all 0.4s;
  width: 200px;
  height: 180px;
  font-weight: bold;
  font-size: 20px;
  color: white;
}

.effect {
  flex-direction: column;
  background: linear-gradient(0deg, rgba(37,27,47,1) 8%, rgba(156,232,166,1) 68%);
  border-radius: 15px;
  width: 600px;
  margin: auto;
  font-size: 36px;
  padding: 20px;
  display: flex;
  border: 3px solid black;
  font-weight: bold;
  font-style: italic;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  margin-top: 100px;
}


.normalp2{
    display: none;
}
.photo{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;
    justify-content: center;
}

.p2{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}